peat: no-repeat;\n}\n\n.hero__inner {\n  text-align: center;\n  margin: 4em auto;\n}\n\n.hero__title {\n  font-size: 2em;\n  font-weight: 600;\n}\n\n.hero__tagline {\n  font-size: 1.2em;\n  margin: 10px;\n}\n\n.hero__inner img {\n  width: 100%;\n}\n\n/*\n *  Restaurants\n */\n\n.restaurants {\n  margin-top: 20px;\n}\n\n.restaurants > h2 {\n  font-size: 2em;\n  font-family: 'Josefin Sans', sans-serif;\n  text-align: center;\n  margin: 20px 0;\n  padding: 5px;\n}\n\n.restaurants > h2::after {\n  content: '';\n  display: block;\n  position: relative;\n  width: 260px;\n  height: 2px;\n  background-color: #282a3a;\n  bottom: -3px;\n  left: calc(50% - 130px);\n}\n\n.restaurant__inner {\n  width: 85%;\n  margin: auto auto;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 1.5em;\n}\n\n.restaurant__item {\n  padding: 1.2em;\n  box-shadow: 0px 2px 5px #735f32;\n  border-radius: 10px;\n}\n\n.restaurant__title {\n  text-align: center;\n  font-size: 1.3em;\n  margin: 0;\n}\n\n.restaurant__title a {\n  display: block;\n  min-width: 44px;\n  min-height: 44px;\n  color: #000;\n  text-decoration: none;\n  margin: 0;\n}\n\n.restaurant__rate {\n  display: block;\n  position: relative;\n  top: 100px;\n  width: fit-content;\n  padding: 10px 20px;\n  margin: 0;\n  font-weight: bolder;\n  color: yellow;\n  background-color: black;\n  opacity: 0.8;\n  box-sizing: border-box;\n}\n\n.restaurant__item img {\n  width: 100%;\n  border: #888888;\n  z-index: 1;\n}\n\n.restaurant__city {\n  font-size: 14px;\n  font-weight: bold;\n  margin: 5px auto;\n}\n\n.restaurant__description {\n  font-size: 14px;\n  line-height: 18px;\n}\n\n/*\n *  Contact Us\n */\n\n.contact-us > h2 {\n  font-size: 2em;\n  font-family: 'Josefin Sans', sans-serif;\n  text-align: center;\n  margin: 20px 0;\n  padding: 5px;\n}\n\n.contact-us > h2::after {\n  content: '';\n  display: block;\n  position: relative;\n  width: 200px;\n  height: 2px;\n  background-color: #282a3a;\n  bottom: -3px;\n  left: calc(50% - 100px);\n}\n\n.form {\n  width: 100%;\n  padding: 10px;\n  background-color: #282a3a;\n}\n\n.form__item {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 10px;\n}\n\n.form__item label {\n  font-size: 1.3em;\n  margin-bottom: 2px;\n  color: #fff;\n}\n\n.form__item input,\n.form__item textarea {\n  min-width: 44px;\n  min-height: 44px;\n  padding: 5px;\n  border-radius: 5px;\n  border: none;\n}\n\n.form__item input:focus,\n.form__item textarea:focus,\n.button:focus {\n  outline: auto 1px #c69749;\n}\n\n.button {\n  padding: 5px;\n  min-width: 44px;\n  min-height: 44px;\n  font-weight: 600;\n  border-radius: 5px;\n  border: none;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/*\n *  Footer\n */\n\nfooter {\n  width: 100%;\n  background-color: #282a3a;\n  color: #fff;\n  min-height: 30px;\n}\n\nfooter p {\n  text-align: center;\n  padding: 0.8em;\n}\n\n.placeholder {\n  font-weight: lighter;\n  color: rgba(0, 0, 0, 0.5);\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n/*\n *  Detail Restaurant\n */\n\n.restaurant {\n  width: 90%;\n  margin: 20px auto;\n  padding: 8px;\n  box-sizing: border-box;\n  box-shadow: 0px 2px 4px #000;\n}\n\n.restaurant__name {\n  text-align: center;\n  font-size: 1.6em;\n  margin-bottom: 10px;\n  font-weight: 600;\n}\n\n.restaurant__img {\n  width: 100%;\n}\n\n.restaurant__info {\n  box-sizing: border-box;\n  border: 1px solid #555555;\n  border-radius: 5px;\n  padding: 5px;\n}\n\n.restaurant__info h3 {\n  text-align: center;\n  background-color: #e0b56f;\n  padding: 3px;\n  margin-bottom: 5px;\n  font-weight: 600;\n}\n\n.restaurant__info h4 {\n  margin-bottom: 3px;\n}\n\n.restaurant__info > p {\n  margin-bottom: 5px;\n}\n\