ontent: center;\n  padding: 10px;\n  margin-top: 5px;\n}\n\n.review__add a {\n  display: block;\n  min-width: 44px;\n  min-height: 44px;\n  text-decoration: none;\n  font-weight: bold;\n  font-size: 1.2em;\n  color: #000;\n  padding: 10px;\n  border-radius: 5px;\n  background-color: #fcb84c;\n  box-sizing: border-box;\n  box-shadow: 0 2px 2px #000;\n}\n\n.review__add a:hover {\n  box-shadow: none;\n  background-color: #e0a648;\n}\n\n/*\n *  Favorite Restaurant\n */\n\n.content {\n  margin-bottom: 20px;\n}\n\n.content__heading {\n  text-align: center;\n  margin: 20px;\n  font-size: 1.6em;\n}\n\n/*\n *  Review\n */\n\n.review {\n  margin: 20px auto;\n  max-width: 400px;\n  padding: 10px;\n  box-shadow: 0px 2px 4px #000;\n}\n\n.review h2 {\n  font-size: 2em;\n  font-family: 'Josefin Sans', sans-serif;\n  text-align: center;\n  margin-bottom: 10px;\n}\n\n.review__item {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 10px;\n}\n\n.review__item label {\n  font-size: 1.3em;\n  margin-bottom: 2px;\n}\n\n.review__item input,\n.review__item textarea,\n.btn__field button {\n  min-width: 44px;\n  min-height: 44px;\n  padding: 5px;\n  border-radius: 5px;\n  border: 1px solid #e0b56f;\n}\n\n.btn__field {\n  display: flex;\n  justify-content: end;\n}\n\n.btn__field button {\n  background-color: #fcb84c;\n  box-sizing: border-box;\n  box-shadow: 0 2px 2px #000;\n}\n\n.btn__field button:hover {\n  cursor: pointer;\n  background-color: #e0a648;\n  box-shadow: none;\n}\n\n.review__item input:focus,\n.review__item textarea:focus,\n.btn__field button:focus {\n  outline: auto 1px #e0a648;\n}\n\n/*\n *  Loading bar\n */\n\n.loader-container {\n  position: fixed;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  top: 0;\n  right: 0;\n  left: 0;\n  bottom: 0;\n  background-color: rgba(0, 0, 0, 0.2);\n  z-index: 999;\n}\n\n.lds-ellipsis {\n  display: inline-block;\n  position: relative;\n  width: 80px;\n  height: 80px;\n}\n.lds-ellipsis div {\n  position: absolute;\n  top: 33px;\n  width: 13px;\n  height: 13px;\n  border-radius: 50%;\n  background: #8a2387; /* fallback for old browsers */\n  background: -webkit-linear-gradient(\n    to right,\n    #f27121,\n    #e94057,\n    #8a2387\n  ); /* Chrome 10-25, Safari 5.1-6 */\n  background: linear-gradient(\n    to right,\n    #f27121,\n    #e94057,\n    #8a2387\n  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */\n\n  animation-timing-function: cubic-bezier(0, 1, 1, 0);\n}\n.lds-ellipsis div:nth-child(1) {\n  left: 8px;\n  animation: lds-ellipsis1 0.6s infinite;\n}\n.lds-ellipsis div:nth-child(2) {\n  left: 8px;\n  animation: lds-ellipsis2 0.6s infinite;\n}\n.lds-ellipsis div:nth-child(3) {\n  left: 32px;\n  animation: lds-ellipsis2 0.6s infinite;\n}\n.lds-ellipsis div:nth-child(4) {\n  left: 56px;\n  animation: lds-ellipsis3 0.6s infinite;\n}\n@keyframes lds-ellipsis1 {\n  0% {\n    transform: scale(0);\n  }\n  100% {\n    transform: scale(1);\n  }\n}\n@keyframes lds-ellipsis3 {\n  0% {\n    transform: scale(1);\n  }\n  100% {\n    transform: scale(0);\n  }\n}\n@keyframes lds-ellipsis2 {\n  0% {\n    transform: translate(0, 0);\n  }\n  100% {\n    transform: translate(24px, 0);\n  }\n}\n\n.skeleton {\n  animation: skeleton-loading 1s linear infinite alternate;\n}\n\n@keyframes skeleton-loading {\n  0% {\n    background-color: hsl(200, 20%, 80%);\n  }\n  100% {\n    background-color: hsl(200, 20%, 95%);\n  }\n}\n"],"sourceRoot":""}]);
